                :customerEmail="EmailId" visibility="maximized" v-on:new-event="handleNewEvent" />

            <VueEasyLightbox :visible="visibleRef" :imgs="imgpath" @hide="onHide">
            </VueEasyLightbox>
        </section>
    </div>
</template>

