import DataService from '../../services/DataService';

import Search from "../../../src/views/HelpAndGuide/Search.vue";

import { storeToRefs } from "pinia";
import { useloggedUserInfo } from "@/stores/UserInfo";
const { GetUserInfo } = storeToRefs(useloggedUserInfo());
import { LiveChatWidget } from '@livechat/widget-vue/v2'
import VueEasyLightbox from 'vue-easy-lightbox'


export default {

    name: 'HelpArticlesPerModule',

    props: {
        IsLiveChatShowHelpGuidePage: Boolean
    },
    components: {

        Search,
        LiveChatWidget,
        VueEasyLightbox
    },
    data() {

        return {
            visibleRef: false,
            imgpath: '',
            ModulesList: [],
            HelpArticlePerModuleList: [],
            IsDataNotFountShow: false,
            PlaceHolderText: 'Search by Keyword',
            IsLiveChatShowArticlePage: false,
            ModuleId: 0,
            isLoading: false,
            Path: Boolean,
            Id: null,
            Module_Id: null,
            SelectModuleId: '',
            activeAccordion: null,
            DefaultModuleId: 0,
            ModuleName: '',
            ArticlesContent: '',
            IsDataNotFoundShow: false,
            IsDataShow: true,
            UserID: null,
            UserName: null,
            CompanyName: null,
            EmailId: null,

        }
    },
    created: async function () {
           
        this.UserID = this.GetUserInfo.ID;
        this.UserName = this.GetUserInfo.UserName;
        this.CompanyName = this.GetUserInfo.CompanyName;
        this.EmailId = this.GetUserInfo.Email;

        this.Id = this.$route.params.Id;
        this.Module_Id = this.$route.params.ModuleId;
        await this.ModuleList();
        if (this.Module_Id > 0) {
            await this.ArticleListPerModule(this.Module_Id);
        }
        else {
            await this.ArticleListPerModule(this.DefaultModuleId);
        }

        this.activeAccordion = this.Id;

    },
    mounted() {
        
        window.handleClick = this.handleClick;
    },
    methods: {
        handleClick(event) {            
           
            this.imgpath = event.src;
            this.visibleRef = true;
            
        },

        onHide() {
            
            this.visibleRef = false;
        },
        OpenChat: function () {

            var vm = this;
