            //             },
            //             init: function () {
            //                 var n = t.createElement("script");
            //                 n.async = !0, n.type = "text/javascript", n.src = "https://cdn.livechatinc.com/tracking.js", t.head.appendChild(n)
            //             }
            //         };
            //         !n.__lc.asyncInit && e.init(), n.LiveChatWidget = n.LiveChatWidget || e
            //     }(window, document, [].slice))
            //     setTimeout(() => {
            //         LiveChatWidget.call("CompanyId", vm.CompanyName);
            //         LiveChatWidget.call("UserID", vm.UserID);
            //         LiveChatWidget.call("UserName", vm.UserName);
            //         LiveChatWidget.call("EmailId", vm.EmailId);
            //         LiveChatWidget.call("set_customer_name", vm.UserName);

            //         LiveChatWidget.call("set_session_variables", {
            //             UserName: vm.UserName,
            //             CompanyName: vm.CompanyName,
            //             UserID: vm.UserID,
            //             EmailId: vm.EmailId
            //         });

            //         function onReady(initialData) {
            //             var state = initialData.state;
            //             var customerData = initialData.customerData.sessionVariables;
            //             LiveChatWidget.call("set_session_variables", {
            //                 UserName: vm.UserName,
            //                 CompanyName: vm.CompanyName,
            //                 UserID: vm.UserID,
            //                 Email: vm.EmailId
            //             });


            //         }
            //         LiveChatWidget.call("set_customer_name", vm.UserName);
            //         LiveChatWidget.on('ready', onReady)
            //     }, 1000);
            // }, 1000);

        },
        ModuleList: async function () {

            var vm = this;
            vm.isLoading = true;

            await DataService.GetModulesSubModules().then(function (response) {

                if ((response.data != null && response.data != undefined) && (response.data.Data.length > 0 && response.data.Data != null)) {
                    response.data.Data.forEach((value, index) => {

                        let myobj = {
                            ModuleId: value.ModuleId,
                            ModuleName: value.ModuleName,
                            icon: '<em class="fa ' + value.Icon + '" aria-hidden="true"></em>'

                        }
                        vm.ModulesList.push(myobj);
                        vm.isLoading = false;

                    })
                    vm.DefaultModuleId = vm.ModulesList[0].ModuleId;

                    vm.isLoading = false;
                }
                else {
                    vm.isLoading = false;
                }
            });
        },
        ArticleListPerModule: async function (Id) {
             
            var vm = this;
            vm.ModuleId = Id;
            vm.isLoading = true;
            var params = `search=&userType=${GetUserInfo.value.UserType}&articleType=HelpArticle&moduleId=${Id}`

            await DataService.GetArticleList(params).then(function (response) {
                if (response.data != null && response.data.length > 0) {
                    vm.HelpArticlePerModuleList = response.data;

                    vm.HelpArticlePerModuleList.forEach((item, index) => {

                        item.articlecontent = item.articlecontent.replaceAll('<img ', '<img onclick="handleClick(this)"');


                    });


                    vm.ModuleName = vm.HelpArticlePerModuleList[0].ModuleName;
                    vm.isLoading = false;
                    vm.IsDataShow = true;
                    vm.IsDataNotFoundShow = false;
                }
                else {
                    vm.HelpArticlePerModuleList = [];
                    vm.ModuleName = '';
                    vm.isLoading = false;
                    vm.IsDataNotFoundShow = true;
                    vm.IsDataShow = false;


                }
            });
            vm.SelectModuleId = Id;

            const currentRoute = vm.$route.fullPath;
            const targetRoute = `/Help/HelpArticlesPerModule/${Id}/0}`;

            if (currentRoute != targetRoute) {
                vm.$router.push({ name: "HelpArticlesPerModule", params: { ModuleId: Id, Id: vm.ModuleId == vm.$route.params.ModuleId ? vm.$route.params.Id : 0 } }).catch(() => { });
            }


        },

        DownloadUsermanual: async function (Id) {

            var vm = this;
            vm.isLoading = true;
            var FileName = "Document.pdf";
            var params = `FileUrl=&ModuleId=${Id}`


            await DataService.DownloadSingleFile(params).then((response) => {

                if (response.data != null) {

                    var fileURL = window.URL.createObjectURL(new Blob([response.data]));
                    var fileLink = document.createElement('a');
                    fileLink.href = fileURL;

                    fileLink.setAttribute('download', FileName);
                    document.body.appendChild(fileLink);
                    fileLink.click();
                    vm.isLoading = false;
                }
                else {
                    vm.ShowAlert(vm.$t("FileNotDownload"), "warning", true, vm.$t("Alert"));
                    vm.isLoading = false;
                }
            })
        },
        SearchData: async function (data) {

            var vm = this;
            if (data != "") {
                vm.isLoading = true;
                var params = `search=${data}&userType=${GetUserInfo.value.UserType}&articleType=HelpArticle&moduleId=`;

                await DataService.GetArticleList(params).then(function (response) {

                    if (response.data != null && response.data.length > 0) {
                        vm.HelpArticlePerModuleList = response.data;
                        vm.isLoading = false;
                        vm.IsDataShow = true;
                        vm.IsDataNotFoundShow = false;
                    }
                    else {

                        vm.isLoading = false;
                        vm.IsDataShow = false;
                        vm.IsDataNotFoundShow = true;

                    }
                });

            }
            else {
                vm.ShowAlert(vm.$t("FillFealds"), "warning", true, vm.$t("Alert"));
            }

        },
        GetArticleContent: async function (ArticlesId) {

            var vm = this;
            vm.ArticlesContent = '';
            var params = `ArticleId=${ArticlesId}&articleType=HelpArticle`;
            await DataService.GetArticleDetailById(params).then(function (response) {

                if (response.data != null && response.data.length > 0) {
                    vm.ArticlesContent = response.data.article_content;
                }
            });
        },

        GetUrl: function (ArticleId, ModuleId) {


            var vm = this;

            const currentRoute = vm.$route.fullPath;
            const targetRoute = `/Help/HelpArticlesPerModule/${ModuleId}/${ArticleId}}`;

            if (currentRoute !== targetRoute) {
                vm.$router.push({ name: "HelpArticlesPerModule", params: { ModuleId: ModuleId, Id: ArticleId } }).catch(() => { });;
            }
        },       

    }
}
</script>

